.wch-container {
  cursor: pointer;
  width: 100%;
  height: 100%;
  z-index: 0;
}
.wch-container.active {
  outline: 3px solid #fb0;
}
.wch-container .wch-rnd-wrap {
  box-sizing: border-box;
  border: none;
}
.wch-container .wch-grid-line {
  width: 100%;
  height: 100%;
}
.wch-container .wch-grid-line.hide {
  display: none;
}
