.wch-container {
  height: 100%;
}
.wch-container .wch-content-box {
  height: 100%;
}
.wch-container .wch-content-box .wch-row1-box {
  height: 5%;
  background: #333;
  border-top: 1px solid gray;
}
.wch-container .wch-content-box .wch-row1-box .wch-handle-btn {
  font-size: 14px;
  color: white;
  cursor: pointer;
}
.wch-container .wch-content-box .wch-row1-box .wch-handle-btn.hide {
  display: none;
}
.wch-container .wch-content-box .wch-row2-box {
  height: 94.7%;
}
.wch-container .wch-content-box .wch-row2-box .wch-col2-box {
  height: 100%;
}
.wch-container .wch-content-box .wch-row2-box .wch-col2-box .wch-layout-inside {
  height: 100%;
}
.wch-container .wch-content-box .wch-row2-box .wch-col2-box .wch-layout-inside .wch-sider-left {
  box-shadow: 0px 1px 3px black inset;
  background: #f0f2f4;
}
.wch-container .wch-content-box .wch-row2-box .wch-col2-box .wch-layout-inside .wch-sider-left .wch-divider {
  background: #333;
  height: 2px;
  margin-bottom: 0;
}
.wch-container .wch-content-box .wch-row2-box .wch-col2-box .wch-layout-inside .wch-content-inside {
  height: 100%;
}
.wch-container .wch-content-box .wch-row2-box .wch-col2-box .wch-layout-inside .wch-content-inside .wch-touch-box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.wch-container .wch-content-box .wch-row2-box .wch-col2-box .wch-layout-inside .wch-content-inside .wch-equilizer-box {
  width: 35px;
  height: 40px;
  margin-top: -30px;
  cursor: pointer;
  margin-left: -2px;
}
.wch-container .wch-content-box .wch-row3-box {
  height: 0.3%;
  background: #333;
}
.wch-container .wch-box-shadow {
  box-shadow: 0px 1px 3px darkgrey inset;
}
