.wch-container {
  width: 100%;
  height: 100%;
}
.wch-container .wch-header-wrap {
  background: #edeff3;
  padding: 0;
  margin: 0;
  line-height: normal;
}
.wch-container .react-resizable-handle {
  position: absolute;
  box-sizing: border-box;
  height: 3px !important;
  background: #333;
  width: 100%;
}
.wch-container .react-resizable-handle-ns {
  bottom: 0;
  right: 0;
  cursor: ns-resize !important;
  height: 100%;
}
