.wch-container {
  position: fixed;
  background: white;
  box-shadow: 0px 2px 10px #999999;
  margin: 0;
  padding: 0;
  z-index: 9999999;
}
.wch-container-menu {
  margin: 0;
  padding: 0;
}
.wch-container-menu-option {
  padding: 6px 0px 6px 10px;
  min-width: 150px;
  cursor: default;
  font-size: 14px;
  list-style: none;
}
.wch-container-menu-option:hover {
  background: #333;
  color: white;
}
.wch-container-menu-option:active {
  color: #e9e9e9;
  background: #333;
}
.wch-container-menu-option-disabled {
  color: #999999;
  pointer-events: none;
}
.wch-container-menu-option span {
  padding-left: 10px;
}
.wch-container-menu--separator {
  width: 100%;
  height: 1px;
  background: #CCCCCC;
  margin: 0 0 0 0;
}
