.wch-container {
  cursor: move;
  width: 100%;
  height: 100%;
  position: absolute;
  box-sizing: border-box;
  border: 1px solid darkgrey;
}
.wch-container.active {
  border: 2px solid #fb0;
}
.wch-container img {
  border: none;
}
.wch-container .wch-rnd-mark {
  background: darkgrey;
  color: white;
  cursor: move;
  height: 20px;
  width: 50px;
  text-align: center;
  font-size: 12px;
  line-height: 20px;
  position: absolute;
  border: none;
  outline: none;
}
.wch-container .wch-rnd-mark.active {
  background: #fb0;
  color: white;
}
