.react-resizable-handle {
  position: absolute;
  box-sizing: border-box;
  height: 100%;
  width: 1px;
}
.react-resizable-handle:nth-child(2) {
  height: 0px;
  width: 0px;
}
.react-resizable-handle-ew {
  bottom: 0;
  right: 0;
  cursor: ew-resize;
  height: 100%;
}
.odd {
  background: #f8f8f8;
  height: 15px;
  line-height: 15px;
  color: black;
}
.enev {
  background: #ffffff;
  height: 15px;
  line-height: 15px;
  color: black;
}
.inputNum {
  border: none;
}
.inputNum.inputodd {
  background: #f8f8f8;
}
.table thead tr {
  box-shadow: -1px -1px 1px gray;
}
.table thead tr:first-child th {
  background: #333;
  color: white;
  border-right: 1px solid gray;
}
