.equilizer {
  width: 100%;
  height: 100%;
  transform: rotate(180deg);
  margin: 0 auto;
}
.bar {
  fill: #333;
  width: 10%;
  animation: equalize 4s 0s infinite;
}
.bar:nth-child(1) {
  animation-delay: -1.9s;
}
.bar:nth-child(2) {
  animation-delay: -2s;
}
.bar:nth-child(3) {
  animation-delay: -2.3s;
}
.bar:nth-child(4) {
  animation-delay: -2.4s;
}
.bar:nth-child(5) {
  animation-delay: -2.1s;
}
.bar:nth-child(6) {
  animation-delay: -2.7s;
}
.bar:nth-child(7) {
  animation-delay: -1.8s;
}
@keyframes equalize {
  0% {
    height: 60%;
  }
  4% {
    height: 50%;
  }
  8% {
    height: 40%;
  }
  12% {
    height: 30%;
  }
  16% {
    height: 20%;
  }
  20% {
    height: 30%;
  }
  24% {
    height: 40%;
  }
  28% {
    height: 10%;
  }
  32% {
    height: 40%;
  }
  36% {
    height: 60%;
  }
  40% {
    height: 20%;
  }
  44% {
    height: 40%;
  }
  48% {
    height: 70%;
  }
  52% {
    height: 30%;
  }
  56% {
    height: 10%;
  }
  60% {
    height: 30%;
  }
  64% {
    height: 50%;
  }
  68% {
    height: 60%;
  }
  72% {
    height: 70%;
  }
  76% {
    height: 80%;
  }
  80% {
    height: 70%;
  }
  84% {
    height: 60%;
  }
  88% {
    height: 50%;
  }
  92% {
    height: 60%;
  }
  96% {
    height: 70%;
  }
  100% {
    height: 80%;
  }
}
