.wch-container {
  height: 100%;
  border: 3px solid #333;
}
.wch-container .wch-head {
  background: #333;
  padding-top: 0;
  padding-bottom: 0;
}
.wch-container .wch-head span {
  color: white !important;
  font-size: 16px;
}
.wch-container .wch-box-shadow {
  box-shadow: 0px 1px 3px black inset;
}
