.wch-container {
  boxShadow: '0px 0px 5px black';
  cursor: pointer;
  width: 100%;
  height: 100%;
  position: absolute;
  cursor: move;
}
.wch-container.active {
  outline: 3px solid black;
}
.wch-container .wch-rnd-wrap {
  background: black;
  border: 2px solid darkgrey;
}
.wch-container .wch-rnd-wrap .wch-rnd-mark {
  background: darkgrey;
  color: white;
  cursor: move;
  height: 30px;
  width: 30px;
  text-align: center;
  font-size: 20px;
  line-height: 30px;
}
.wch-container .wch-rnd-wrap:hover {
  border: 2px solid darkgrey;
}
.wch-container .wch-rnd-wrap.active {
  border: 2px solid lightseagreen;
}
.wch-container .wch-rnd-wrap.active .wch-rnd-mark {
  background: lightseagreen;
}
.wch-container .wch-rnd-wrap.active:hover {
  border: 2px solid lightseagreen;
}
