.wch-container {
  height: 100%;
  padding-right: 3px;
}
.wch-container .wch-header-wrap {
  background: #333;
  width: 100%;
  height: 5%;
  border: 1px solid gray;
  border-left: none;
  padding: 0;
  line-height: normal;
  color: white;
  font-size: 20px;
}
.wch-container .wch-header-wrap.active {
  border: none;
  border-top: 1px solid gray;
}
.wch-container .wch-header-wrap .wch-row-box {
  height: 100%;
  padding: 0;
}
.wch-container .wch-header-wrap .wch-row-box .wch-handle-btn {
  font-size: 14px;
  color: white;
  cursor: pointer;
}
.wch-container .wch-header-wrap .wch-row-box .wch-handle-btn.hide {
  display: none;
}
.wch-container .wch-header-wrap .wch-row-box .wch-double-arrow {
  text-align: right;
  cursor: pointer;
  padding-right: 8%;
}
.wch-container .wch-header-wrap .wch-row-box .wch-double-arrow.active {
  font-size: 15px;
  padding-right: 6px;
}
.wch-container .wch-content-wrap .wch-wrap-item {
  font-size: 14px;
  color: #333;
  border-bottom: 1px solid #ddd;
  line-height: 30px;
  cursor: pointer;
}
.wch-container .wch-content-wrap .wch-wrap-item.active {
  background: #fb0;
  color: white;
}
